<template>

    <div class="container">
        <div class="row">
            <div class="col-md-4">
                <p id="pagination" v-if="venues.length">Showing {{ showingFrom }} - {{ showingTo }} out of {{ total }} venues
                    <button :disabled="!hasPreviousPages" v-on:click="onPreviousPage"><font-awesome-icon icon="arrow-left" /></button>
                    <button :disabled="!hasNextPages" v-on:click="onNextPage"><font-awesome-icon icon="arrow-right" /></button>
                </p>
            </div>
        </div>
        <div class="row">
            <div class="col-md-4">
                <div id="venue-list">
                    <ul class="list-unstyled">
                      <li v-for="(venue, index) in venues" class="block-content" @mouseenter="onMouseEnterVenueItem(index)" @mouseleave="onMouseLeaveVenueItem">
                        <h4>{{ venue.name }}</h4>
                        <p v-if="venue.address" class="venue-details">
                            <font-awesome-icon icon="map-marker-alt" class="blue" />
                            <span>{{ venue.address }}</span>
                        </p>
                        <p v-if="venue.telephone" class="venue-details">
                            <font-awesome-icon icon="phone" class="blue" />
                            <span>{{ venue.telephone }}</span>
                        </p>
                        <p class="text-right"><a :href="getVenueLink(venue)" target="_blank" title="More about this venue" class="icon-link purple">
                        More about this venue <font-awesome-icon icon="arrow-right" size="xs" /></a></p>
                      </li>
                    </ul>
                    <div v-if="!venues.length">
                        <p>Sorry, we couldn't find any venues matching your search criteria.</p>
                        <p>Please try again.</p>
                    </div>
                </div>
            </div>
            <div class="col-md-8 text-center" id="map-wrapper">
                <div id="map"></div>
                <button class="btn btn-white btn-square" v-on:click="onSearchThisArea">Search this area</button>
            </div>
        </div>
    </div>



</template>

<script>
import 'leaflet'

const L = window.L;
const axios = require('axios');

export default {
  data() {
    return {
      map: [],
      markers: null,
      total: null,
      defaultCentroid: ['55.3781', '-3.4360'],
      cookie_name: 'sl-location',
      venues: [],
      defaultIcon: L.icon({
          iconUrl: require('../assets/img/marker.png'),
          iconSize: [33, 43],
          iconAnchor: [22, 50],
          popupAnchor: [-6, -50]
      }),
      activeIcon: L.icon({
          iconUrl: require('../assets/img/activeMarker.png'),
          iconSize: [33, 43],
          iconAnchor: [22, 50],
          popupAnchor: [-6, -50]
      }),
      numPerPage: 50,
      page: 1,
      resizeMapToMarkers: true,
      searchQuery: null
    }
  },
  computed: {
    q(){
        return this.$route.query.q
    },
    showingFrom() {
      return ((this.numPerPage * this.page) - this.numPerPage) + 1;
    },
    showingTo() {
      if(this.numPerPage * this.page > this.total){
        return this.total
      }else{
        return this.numPerPage * this.page;
      }
    },
    hasPreviousPages(){
        return this.page > 1
    },
    hasNextPages(){
        return this.numPerPage * this.page < this.total
    },

  },
  watch: {
    venues(val) {
      this.addMarkers();
    },
    page(val) {
        this.updatePage();
    },
    q(val) {
        this.onSearchUpdate();
    },
  },
  methods: {

    searchVenues(searchOptions){
        this.buildSearchQuery(searchOptions);
        this.runSearch();
    },

    buildSearchQuery(searchOptions){

        this.resizeMapToMarkers = true;
        // todo - double firing
        this.page = 1;

        this.searchQuery = {
            'size': this.numPerPage
        }

        if (searchOptions && 'centroid' in searchOptions){
            this.searchQuery['query'] = {
              "function_score": {
                "query": {
                  "bool": {
                    "must": {
                        "match_all": {}
                    },
                  }
                },
                "functions": [
                  {
                    "gauss": {
                        "location": {
                          "origin": { "lat": searchOptions.centroid[0], "lon": searchOptions.centroid[1] },
                          "scale":  "3km"
                        }
                    }
                  },
                ],
                "score_mode": "sum"
              }
            }
        }else{

            this.searchQuery['query'] = {
                bool: {}
            }

            if (searchOptions && 'bounds' in searchOptions){

                this.resizeMapToMarkers = false;

                this.searchQuery.query.bool['filter'] = {
                    "geo_bounding_box" : {
                        "location" : {
                            "top_left" : {
                                "lat" : searchOptions['bounds'].getNorth(),
                                "lon" : searchOptions['bounds'].getWest()
                            },
                            "bottom_right" : {
                                "lat" : searchOptions['bounds'].getSouth(),
                                "lon" : searchOptions['bounds'].getEast()
                            }
                        }
                    }
                }
            }

            if(this.q){
                this.searchQuery.query.bool['must'] = {
                    "multi_match" : {
                          "query": this.q,
                          "fields": [ "name^3", "address" ]
                        }
                }
            }else{
                this.searchQuery.query.bool['must'] = {
                    "match_all" : {}
                }
            }

        }

    },
    runSearch(){
        var vm = this

        axios.get('http://sciencelive.net/venues-search', {
          params: {
            source: JSON.stringify(this.searchQuery),
            source_content_type: 'application/json'
          }
        })
          .then(function (response) {
            // handle success
            vm.total = response.data.hits.total;
            vm.venues= response.data.hits.hits.map(function(hit) {return hit._source;});
          })
          .catch(function (error) {
            // handle error
            console.log(error);
          })
          .then(function () {
            // always executed
          });
    },

    updatePage(){
        this.searchQuery['from'] = this.showingFrom;
        this.runSearch();
    },

    addMarkers() {
      if(!this.map) return;
      this.removeMarkers();
      const map = this.map
      const markers = L.featureGroup();
      this.venues.forEach( (venue) => {
        let marker = L.marker([venue.location.lat, venue.location.lon],  {icon: this.defaultIcon})
            .bindPopup(this.popUp(venue))
        markers.addLayer(marker)
        // .on('click', (el) => alert(el.target))
      })
      map.addLayer(markers)
      if(this.resizeMapToMarkers && this.venues.length){
        map.fitBounds(markers.getBounds());
      }
      this.markers = markers
    },
    popUp(venue){
        return '<strong>' + venue.name + '</strong><br />' + venue.address + '<br /><a href="' + this.getVenueLink(venue) + '" target="_blank" class="icon-link purple">More info</a>'
    },
    removeMarkers() {
        if(this.markers){
            this.map.removeLayer(this.markers);
            this.markers = null;
        }
    },
    getVenueLink(venue) {
        if (venue.url){
            return venue.url
        }else{
            return 'https://www.google.co.uk/search?q=' + venue.name + ' ' + venue.address
        }
    },

    addMap(location){
        const map = L.map('map').setView(this.defaultCentroid, 5)

        // And add map tiles
        L.tileLayer('https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token={accessToken}', {
            id: 'mapbox.streets',
            accessToken: 'pk.eyJ1IjoiYmVuIiwiYSI6ImNqN3V5Y2dzaTBmOXYzM280dmcyd2VmZHcifQ.GhV7tnV9i5DhOFcZ4Vge_g',
            attribution: '',
            minZoom: 2,
            maxZoom: 18
        }).addTo(map);

        this.map = map

    },
    searchByUserLocation(){
        const cookie_location = this.$cookie.get(this.cookie_name);
        if (cookie_location){
            this.searchVenues({centroid: cookie_location.split(',')})
        }else{
            this.getUserLocation();
        }
    },
    getUserLocation() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(this.getLocationSuccess, this.getLocationFailure);
      }else{
        this.searchVenues()
      }
    },
    getLocationSuccess(location) {
        if (location && location.coords){
            const centroid = [location.coords.latitude, location.coords.longitude]
            this.$cookie.set(this.cookie_name, centroid, 7);
            this.searchVenues({centroid: centroid})
        }else{
            this.searchVenues()
        }
    },
    getLocationFailure(error) {
        this.searchVenues()
    },
    onSearchUpdate () {
      this.searchVenues();
    },
    onSearchThisArea(e){
        this.searchVenues({bounds: this.map.getBounds()})
    },
    onPreviousPage(){
        if(this.page > 0){
            this.page -= 1
        }
    },
    onNextPage(){
        this.page += 1
    },
    onMouseEnterVenueItem(index){
        const i = Object.keys(this.markers._layers)[index]
        this.markers._layers[i].setIcon(this.activeIcon);
        this.markers._layers[i]._bringToFront()
    },
    onMouseLeaveVenueItem(event){
        for (var i in this.markers._layers) {
            this.markers._layers[i].setIcon(this.defaultIcon);
        }
        event.stopPropagation()
    }
  },
  mounted(){
    this.addMap();
    if(this.q){
        this.searchVenues();
    }else{
        this.searchByUserLocation(this.q);
    }
  }
}
</script>

